import {createSlice, PayloadAction} from "@reduxjs/toolkit";
import { authAPI } from "../Todolist/todolist-api";
import {fetchTodolistsTC} from "../Todolist/todolists-reducer";
import {ThunkDispatch} from "redux-thunk";

export type RequestStatusType = 'idle' | 'loading' | 'succeeded' | 'failed'

const initialState = {
    status: 'loading' as RequestStatusType,
    error: null as string | null
}

const slice = createSlice({
    name: 'app',
    initialState,
    reducers: {
        setAppStatusAC: (state, action: PayloadAction<{ status: RequestStatusType }>) => {
            state.status = action.payload.status
        },
        setAppErrorAC: (state, action: PayloadAction<{ error: string | null }>) => {
            state.error = action.payload.error
        }
    }
})
export const appReducer = slice.reducer

export const {setAppStatusAC, setAppErrorAC} = slice.actions

type InitialStateType = typeof initialState
type ActionsType = ReturnType<typeof setAppStatusAC> | ReturnType<typeof setAppErrorAC>

export const loginTC = () => (dispatch: ThunkDispatch<InitialStateType, any, ActionsType>) => {
    authAPI.login()
        .then(res => {
            debugger
            console.log(res)
            dispatch(fetchTodolistsTC())
        })
}